<template>
	<div class="seller" ref="seller">
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc border-1px" >
					<star class="star" :size="36" :score="seller.score"></star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2 class="title">起送价</h2>
						<div class="price">
							<span class="stress">{{seller.minPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2 class="title">商家配送</h2>
						<div class="price">
							<span class="stress">{{seller.deliveryPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2 class="title">平均配送时间</h2>
						<div class="price">
							<span class="stress">{{seller.deliveryTime}}</span>分钟
						</div>
					</li>
				</ul>
				<div class="favorite">
					<span class="icon-favorite" @click="toggleFavorite" :class="{'active':favorite}"></span>
					<span class="text">{{favoriteText}}</span>
				</div>
			</div>
			<split></split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper border-1px">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul class="supports" v-if="seller.supports">
					<li class="support-item border-1px" v-for="(item,index) in seller.supports">
						<span class="icon" :class="classMap[item.type]"></span>
						<span class="text">{{item.description}}</span>
					</li>
				</ul>
			</div>
			<split></split>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref="picWrapper">
					<ul class="pic-list" ref="picList">
						<li class="pic-item" v-for="item in seller.pics">
							<img :src="item" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="infos">
				<h1 class="title border-1px">商家信息</h1>
				<ul class="info-list">
					<li class="info-item border-1px" v-for="item in seller.infos">{{item}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
  import star from '../star/star';
  import split from '../split/split';
  import BSscroll from 'better-scroll';
  import {saveTolocal, loadFromLocal} from '../../common/js/store.js';
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        favorite: (() => {
          return loadFromLocal(this.seller.id, 'favorite', false);
        })()
      };
    },
    computed: {
      favoriteText () {
        return this.favorite ? '已收藏' : '收藏';
      }
    },
    methods: {
      toggleFavorite () {
        this.favorite = !this.favorite;
        saveTolocal(this.seller.id, 'favorite', this.favorite);
      },
      _initScroll () {
        if (!this.scroll) {
          this.scroll = new BSscroll(this.$refs.seller, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      },
      _initPics () {
        if (this.seller.pics) {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.picList.style.width = width + 'px';
          this.$nextTick(() => {
            if (!this.picScroll) {
              this.picScroll = new BSscroll(this.$refs.picWrapper, {
                scrollX: true,
                eventPassthrough: 'vertical'
              });
            } else {
              this.picScroll.refresh();
            }
          });
        }
      }
    },
    watch: {
      'seller' () {
        this.$nextTick(function () {
          this._initScroll();
          this._initPics();
        });
      }
    },
    mounted () {
      this.$nextTick(() => {
        this._initScroll();
        this._initPics();
      });
    },
    created () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      star,
      split
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixsin.styl"
	.seller
		position:absolute
		top:174px
		left:0
		bottom:0
		width:100%
		overflow:hidden
		.overview
			position:relative
			padding:18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				margin-bottom:8px
			.desc
				padding-bottom:18px
				font-size:0
				border-1px(rgba(7, 17, 27, 0.1))
				.star
					vertical-align:top
					display:inline-block
					margin-right:8px
				.text
					font-size:10px
					color:rgb(77, 85, 93)
					line-height:18px
					vertical-align:top
					margin-right:12px
			.remark
				padding-top:18px
				display:flex
				.block
					flex:1
					text-align:center
					border-right: 1px solid rgba(7, 17, 27, 0.1)
					&:last-child
						border:none
					.title
						font-size:10px
						color:rgb(147, 153, 159)
						line-height:10px
						margin-bottom:4px
					.price
						font-size:10px
						color:rgb(7, 17, 27)
						.stress
							line-height:24px
							font-size:24px
		  .favorite
				position:absolute
				top:18px
				right:11px
				text-align:center
				width:50px
				.icon-favorite
					display:block
					font-size:24px
					line-height:24px
					margin-bottom:4px
					color:#d4d6d9
					&.active
						color:rgb(240, 20, 20)
				.text
					font-size:10px
					color:rgb(77, 85, 93)
					line-height:10px
		.bulletin
			padding:18px 18px 0 18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				margin-bottom:8px
			.content-wrapper
				padding:0 12px 16px 12px
				border-1px(rgba(7, 17, 27, 0.1))
				.content
					font-size:12px
					color:rgb(240, 20, 20)
					line-height:24px
			.supports
				.support-item
					padding:16px 12px
					border-1px(rgba(7, 17, 27, 0.1))
					&:last-child
						border-none()
					.icon
						display:inline-block
						vertical-align:top
						margin-right:6px
						width:16px
						height:16px
						background-size:16px 16px
						background-repeat:no-repeat
						&.decrease
							bg-image('decrease_4')
						&.discount
							bg-image('discount_4')
						&.guarantee
							bg-image('guarantee_4')
						&.invoice
							bg-image('invoice_4')
						&.special
							bg-image('special_4')
					.text
						font-size:12px
						color:rgb(7, 17, 27)
						line-height:16px
		.pics
			padding:18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				margin-bottom:12px
			.pic-wrapper
				overflow:hidden
				width:100%
				white-space:nowrap
				.pic-list
					font-size:0
					.pic-item
						display:inline-block
						margin-right:6px
						width:120px
						height:90
						&:last-child
							margin:0
		.infos
			padding:18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				padding-bottom:12px
				border-1px(rgba(7, 17, 27, 0.1))
			.info-list
				font-size:0
				.info-item
					padding:16px 12px
					font-size:12px
					color:rgb(7, 17, 27)
					line-height:16px
					border-1px(rgba(7, 17, 27, 0.1))
					&:last-child
						border-none()
</style>



