<template>
	<div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<img :src="seller.avatar" width="64" height="64">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">{{seller.description}}/{{seller.deliveryTime}}分钟送达
				</div>
				<div v-if="seller.supports" class="support" >
					<span class="icon" :class="classMap[seller.supports[0].type]"></span>
					<span class="text">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div v-if="seller.supports" class="support-content" @click="showDetial">
					<span class="count">{{seller.supports.length}}个
					</span>
					<span class="icon-keyboard_arrow_right"></span>
				</div>
		</div>
		<div class="bullertin-wrapper" @click="showDetial">
			<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
			<i class="icon-keyboard_arrow_right"></i>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
		<transition name="fade">
			<div v-show="detailShow" class="detail">
				<div class="detail-wrapper clearfix">
					<div class="detail-main">
						<h1 class="name">{{seller.name}}</h1>
						<div class="star">
							<star :size="48" :score="seller.score"></star>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul class="supports" v-if="seller.supports">
							<li class="support-item" v-for="(item,index) in seller.supports">
								<span class="icon" :class="classMap[seller.supports[index].type]"></span>
								<span class="text">{{seller.supports[index].description}}</span>
							</li>
						</ul>
						<div class="title">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin">
							<p class="content">{{seller.bulletin}}</p>
						</div>
					</div>
				</div>
					<div class="detail-close" @click="hideDetial">
						<i class="icon-close"></i>
					</div>
			</div>
		</transition>
	</div>
</template>

<script>
import star from '../../components/star/star.vue';
export default{
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      detailShow: false
    };
  },
  methods: {
    showDetial () {
      this.detailShow = true;
    },
    hideDetial () {
      this.detailShow = false;
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  components: {
    star
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixsin"
	.header
		color:#fff
		position:relative
		overflow:hidden
		background:rgba(7, 17, 27, 0.5)
		.content-wrapper
			position:relative
			padding:24px 12px 18px 24px;
			font-size: 0
			.avatar
				display:inline-block
				vertical-align:top
				img
					border-radius:2px 
			.content
				display:inline-block
				margin-left:16px
				.title
					margin:2px 0 8px 0
					.brand
						display:inline-block
						width:30px
						height:18px
						vertical-align:top
						bg-image('brand')
						background-size:30px 18px
						background-repeat:no-repeat
					.name
						font-size:16px
						font-weight:bold
						line-height:18px
						margin-left:6px;
				.description
					font-size:12px
					line-height:12px;
					margin:8px 0 10px 0;
				.support
					.icon
						vertical-align:top
						display:inline-block
						width:12px
						height:12px
						margin-right:4px
						background-repeat:no-repeat
						background-size:12px 12px;
						&.decrease
							bg-image('decrease_1')
						&.discount
							bg-image('discount_1')
						&.guarantee
							bg-image('guarantee_1')
						&.invoice
							bg-image('invoice_1')
						&.special
							bg-image('special_1')
					.text
						line-height:12px
						font-size:10px 				  	
			.support-content
				position:absolute
				right:12px
				bottom:18px
				height:24px
				line-height:24px
				padding:0 8px
				border-radius:14px
				background:rgba(0, 0, 0, 0.2)
				text-align:center
				.count
					font-size:12px
				.icon-keyboard_arrow_right
					font-size:12px
					line-height:24px
					margin-left:1px
		.bullertin-wrapper
			position:relative
			height:28px
			line-height:28px
			padding:0 22px 0 12px
			white-space:nowrap
			overflow:hidden
			text-overflow:ellipsis
			background-color:rgba(7, 17, 27, 0.2)
			.bulletin-title
				display:inline-block
				margin-top:8px
				vertical-align:top
				width:22px
				height:12px
				line-height:28px
				bg-image('bulletin')
				background-size:22px 12px
				background-repeat:no-repeat
			.bulletin-text
				vertical-align:top
				margin:0 4px
				font-size:10px
				font-weight:200
			.icon-keyboard_arrow_right
				position:absolute
				font-size:10px
				right:10px
				top:8px
		.background
			position:absolute
			top:0
			left:0
			width:100%
			height:100%
			z-index:-1
			filter:blur(10px)
		.detail
			position:fixed
			z-index:100
			width:100%
			height:100%
			overflow:auto
			background:rgba(7, 17, 27, 0.8)
			top:0
			left:0
			opacity:1
			backdrop-
			backdrop-filter: blur(10px)
			&.fade-enter, .fade-leave-active
				opacity:0
				background:rgba(7, 17, 27, 0)
			&.fade-enter-active, .fade-leave-active
				transition: all 0.5s
			.detail-wrapper
				width:100%
				min-height:100%
				.detail-main
					margin-top:64px
					padding-bottom:64px
					.name
						line-height:16px
						text-align:center
						font-size:16px;
						font-weight:700
					.star
						margin-top:18px
						padding:2px 0
						text-align:center
					.title
						display:flex
						width:80%
						margin:28px auto 24px auto
						.line
							flex:1
							position:relative
							top:-6px
							border-bottom:1px solid rgba(255, 255, 255, 0.2)
						.text
							padding:0 12px
							font-size:14px
					.supports
						width:80%
						margin:0 auto
						.support-item
							padding:0 12px
							margin-bottom:12px
							font-size:0
							&.last-child
								margin-bottom:0
							.icon
								display:inline-block
								height:16px
								width:16px
								vertical-align:top
								margin-right:6px
								background-size:16px 16px
								background-repeat:no-repeat
								&.decrease
									bg-image('decrease_2')
								&.discount
									bg-image('discount_2')
								&.guarantee
									bg-image('guarantee_2')
								&.invoice
									bg-image('invoice_2')
								&.special
									bg-image('special_2')
							.text
								font-size:12px
								line-height:16px
					.bulletin
						width:80%
						margin:0 auto
						.content
							font-size:12px
							line-height:24px;
							padding:0 12px;
			.detail-close
				position:relative
				width:32px
				height:32px
				margin:-64px auto 0 auto
				clear:both
				font-size:32px
				font-weight:700
</style>



