<template>
	<transition name="move">
		<div v-show="showFlag" class="food" ref="food">
			<div class="food-content" >
				<div class="image-header">
          <img :src="food.image">
          <div class="back" @click="back">
            <i class="icon-arrow_lift"></i>
          </div>
				</div>
				<div class="content">
					<h1 class="title">{{food.name}}</h1>
					<div class="detail">
						<span class="count">月售{{food.sellCount}}份</span>
						<span class="rating">好评率{{food.rating}}%</span>
					</div>
					<div class="price">
            <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
					<div class="cartcontrol-wrappper">
						<cartcontrol :food="food"></cartcontrol>
					</div>
					<transition name="fade">
						<div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0">加入购物车</div>
					</transition>
				</div>
				<split></split>
				<div class="info" v-show="food.info">
					<h1 class="title">商品介绍</h1>
					<p class="text">{{food.info}}</p>
				</div>
				<div class="info" v-show="!food.info">
					<h1 class="title">商品介绍</h1>
					<p class="text">暂无信息</p>
				</div>
				<split></split>
				<div class="rating">
					<h1 class="title">商品评价</h1>
					<ratingselect @toggle="toggleAll" @select="selectRating" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
					<div class="rating-wrapper">
						<ul v-show="food.ratings && food.ratings.length">
							<li v-show="needShow(rating.rateType,rating.text)"v-for="rating in food.ratings" class="rating-item border-1px">
								<div class="user">
									<span class="name">{{rating.username}}</span>
									<img class="avatar" width="12" height="12" :src="rating.avatar">
								</div>
								<div class="time">{{rating.rateTime | formatDate}}</div>
								<p class="text">
									<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
								</p>
							</li>
						</ul>
						<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>
<script type="text/ecmascript-6">
  import Vue from 'vue';
  import BSscroll from 'better-scroll';
  import cartcontrol from '../cartcontrol/cartcontrol';
  import split from '../split/split';
  import ratingselect from '../ratingselect/ratingselect';
  import {formatDate} from '../../common/js/formatDate.js';
  const ALL = 2;
  export default {
    props: {
      food: {
        type: Object
      }
    },
    data () {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: false,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      };
    },
    methods: {
      show () {
        this.showFlag = true;
        this.selectType = ALL;
        this.onlyContent = false;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BSscroll(this.$refs.food, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      selectRating (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggleAll () {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      needShow (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      back () {
        this.showFlag = false;
      },
      addFirst (event) {
        if (!event._constructed) {
          return;
        }
        Vue.set(this.food, 'count', 1);
      }
    },
    filters: {
      formatDate (time) {
        let date = new Date(time);
        return formatDate(date);
      }
    },
    components: {
      cartcontrol,
      split,
      ratingselect
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/mixsin.styl"
	.food
		position: fixed
		left: 0
		top: 0
		bottom: 48px
		z-index: 30
		width: 100%
		background: #fff
		transform: translate3d(0, 0, 0)
		&.move-enter-active, &.move-leave-active
			transition: all 0.2s linear
		&.move-enter, &.move-leave-active
			transform: translate3d(100%, 0, 0)
		.image-header
			position: relative
			width: 100%
			height: 0
			padding-top: 100%
			img
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
			.back
				position: absolute
				top: 10px
				left: 0
				.icon-arrow_lift
					display: block
					padding: 10px
					font-size: 20px
					color: #fff
		.content
			position:relative
			padding:18px
			.title
				font-size:14px
				font-weight:700
				color:rgb(7, 17, 27)
				line-height:14px
				margin-bottom:8px
			.detail
				font-size:0px
				line-height:10px
				margin-bottom:18px
				height:10px
				.rating, .count
					color:rgb(147, 153, 159)
					font-size:10px
					margin-right:12px
			.price
				font-weight:700
				line-height:24px
				.now
					font-size:14px
					color:rgb(240, 20, 20)
					margin-right:14px
				.old
					font-size:10px
					color:rgb(147, 153, 159)
					text-decoration:line-through
			.cartcontrol-wrappper
				position:absolute
				right:12px
				bottom:12px
			.buy
				position: absolute
				right: 18px
				bottom: 18px
				z-index: 10
				height: 24px
				line-height: 24px
				padding: 0 12px
				box-sizing: border-box
				border-radius: 12px
				font-size: 10px
				color: #fff
				background: rgb(0, 160, 220)
				opacity: 1
				&.fade-enter-active, &.fade-leave-active
					transition: all 0.2s
				&.fade-enter, &.fade-leave-active
					opacity: 0
					z-index: -1
		.info
			padding:18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				margin-bottom:6px
			.text
				font-size:12px
				color:rgb(77, 85, 93)
				line-height:24px
				padding:0 8px
		.rating
			padding-top:18px
			.title
				font-size:14px
				color:rgb(7, 17, 27)
				line-height:14px
				margin:0 0 6px 18px
			.rating-wrapper
				padding:0 18px
				.rating-item
					position:relative
					padding:16px 0
					border-1px(rgba(7, 17, 27, 0.1))
					.user
						position:absolute
						right:0
						top:16px
						line-height:12px
						font-size:0
						.name
							display:inline-block
							vertical-align:top
							font-size:10px
							color:rgb(147, 153, 159)
							margin-right:6px
					.time
						font-size:10px
						color:rgb(147, 153, 159)
						line-height:12px
						margin-bottom:6px
					.text
						font-size:12px
						color:rgb(7, 17, 27)
						line-height:16px
						.icon-thumb_up
							color:rgb(0, 160, 220)
							line-height:16px
							margin-right:4px
							font-size:10px
						.icon-thumb_down
							color:rgb(147, 153, 159)
							line-height:16px
							margin-right:4px
							font-size:10px
				.no-rating
					padding:16px 0
					font-size:12px
					color:rgb(147, 153, 159)
</style>




